* {
  background-color: rgb(247, 247, 247);
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  
}

.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  width: 1700px;
  margin-top: 0.5%;
  margin-bottom: 0.5%;
}

.nav > * {
  margin: 0%;
  padding: 0%;
  background-color: white;
}

.name {
  font-family: 'Bangers', cursive;
  font-weight: 100;
  color: rgb(24, 24, 24);
}

.converter {
  font-family: 'Roboto', sans-serif;
  font-size: 1.15rem;
  font-weight: 600;
  color: rgb(24, 24, 24);
}

.searchbar {
  font-family: 'Bangers', cursive;
  font-size: 1.1rem;
  outline: none;
  width: 250px;
  height: 25px;
  border: solid transparent;
  border-radius: 10px;
  background-color: rgba(237, 237, 237, 0.781);
  padding-left: 5px;
}

/*Coin header*/
.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 0.7%;
}

.header > * {
  margin: 0;
  padding: 0;
}

.header {
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: flex-start;
  width: 1100px;
  font-weight: 600;
  font-size: 0.9rem;
}

.header-rank {
  flex-basis: 6%;
  text-align: right;
}

.header-name {
  margin-left: 25px;
  flex-basis: 35%;
}

.header-price {
  margin-left: 10px;
  flex-basis: 15%;
}

.header-mcp {
  margin-left: 25px;
  flex-basis: 20%;
}

.header-ath {
  margin-left: 40px;
  flex-basis: 10%;
}

/*Coin price display*/
.container {
  display: flex;
  justify-content: center;
}

.container-flexbox {
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  width: 1100px;
  height: 50px;
  font-size: 0.95rem;
  font-weight: 600;
  color: rgba(55, 54, 54, 0.834);
  border-bottom: 1px solid rgb(202, 199, 199);
}

.coin-rank {
  font-weight: 600;
  flex-basis: 6%;
  text-align: right;
  font-size: 0.85rem;
  margin-right: 10px;
  color: black;
}

.coin-picture {
  width: 25px;
  height: 25px;
}

.coin-name {
  flex-basis: 15%;
  text-align: left;
}

.coin-symbol {
  flex-basis: 5%;
  text-align: left;
  font-size: 0.75rem;
}

.coin-price {
  flex-basis: 15%;
  text-align: right;
}

.coin-mcp {
  flex-basis: 20%;
  text-align: right;
}

.coin-ath {
  flex-basis: 15%;
  text-align: right;
}

.coin-percminus {
  flex-basis: 5%;
  text-align: right;
  color: rgb(190, 34, 34);
}

.coin-percplus {
  flex-basis: 5%;
  text-align: right;
  color: rgb(12, 124, 12);
}